.logo{
    text-decoration: none;

    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    color: $text-color-logo;

    @media (min-width: $mobile) {

    font-size: 24px;
    line-height: 33px;
    }
    
    @media screen and (min-width: $tablet) {
    font-size: 32px;
    line-height: 44px;
    }
    
    @media screen and (min-width: $desktop) {
    font-size: 42px;
    line-height: 57px;
    }

}
